export default {
	data() {
		return {
			school: '',
			userType: 4,
			userText: '',
			username:'',
		}
	},
	onLoad:function(){
		this.school = uni.getStorageSync('school')
		this.username = uni.getStorageSync('username')
	},
	onShow() {
		if (uni.getStorageSync('token')) {
			this.getUserInfo()
		} else {
			uni.switchTab({
				url: '/pages/login/login'
			})
		}
	},
	methods: {
		logout() {
			//用户退出，清楚缓存信息
			uni.removeStorageSync('token')
			uni.removeStorageSync("userType")
			uni.removeStorageSync("uid")
			uni.removeStorageSync("academy_id")
			uni.removeStorageSync("major_id")
			uni.removeStorageSync("grade_id")
			uni.removeStorageSync("stu_num")
			uni.removeStorageSync("stu_name")
			uni.removeStorageSync("username")
			/*
			uni.navigateTo({
				url: '/pages/login/login'
			});
			*/
			// 切换到登录页面
			uni.switchTab({
				url: '/pages/login/login'
			});
			
			
		},
		setUserText(userType) {
			const userOptionDB = {
				0: '老师',
				1: '学生',
				// 2: '家长',
				// 3: '管理员',
			}
			return userOptionDB[userType];
		},
		getUserInfo() {
			console.log("getUserInfo begin")
			uni.showLoading({
				title: '加载中...'
			});

			let token = uni.getStorageSync('token')

			if (token) {
				uniCloud.callFunction({
					name: 'validateToken',
					data: {
						token: uni.getStorageSync('token')
					}
				}).then((res) => {
					console.log("validateToken", res.result.data)
					try {
						const {
							userType,
							school
						} = res.result.data;
						this.school = school;
						this.userType = userType;
						this.userText = this.setUserText(userType)
					} catch (e) {
						throw new Error("参数错误请重新登录");
					}
					uni.hideLoading()
				}).catch((err) => {
					uni.hideLoading()
					uni.showModal({
						content: '请求云函数发生错误，' + err.message,
						showCancel: false
					})
				})
			} else {
				uni.navigateTo({
					url: "../login/login"
				})
			}
		}
	}
}
